body {
  position: relative;
  font-family: OpenSans;
  font-size: 16px;
  font-weight: 400;
  line-height: 21.79px;
  text-align: left;
  color: #585858;
}

html {
  scroll-behavior: smooth;
}

h1,
h2,
h3,
h4,
h5,
