}

* .visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
}

.section {
  margin: 0 auto;
  padding: 49px 20px;
  // border-top: 70px solid transparent;
  // margin-top: -70px;
  position: relative;

  @media (max-width: 1060px) {
    padding: 39px 20px;
  }

  @media (max-width: 660px) {
    padding: 30px 10px;
  }

  // добавляем отступ сверху для якорной ссылки:
  .section__ancor {
    position: absolute;
    top: -70px;
    left: 0;
    width: 100%;
  }
}

.section__container {
  display: grid;
  gap: 50px;

  @media (max-width: 1060px) {
    gap: 37px;
  }

  @media (max-width: 660px) {
    gap: 23px;
  }
}

.main {
  @media (max-width: 760px) {
    border-top: 60px solid transparent;
  }
  &.main_scroll {
    border-top: 181px solid transparent;

    @media (max-width: 1060px) {
      border-top: 172px solid transparent;
    }
  }
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}

.section-title {
  font-family: Gilroy;
  font-size: 32px;
  font-weight: 600;
  line-height: 39.2px;
  color: #052b2e;

  @media (max-width: 1060px) {
    font-size: 30px;
    line-height: 36.75px;
  }

  @media (max-width: 660px) {
    font-size: 26px;
    line-height: 31.85px;
  }
}

.top-block {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 50px;

  @media (max-width: 1060px) {
    gap: 25px;
  }

  @media (max-width: 660px) {
    gap: 10px;
  }

  .top-block__title {
    width: max-content;

