.input {
  padding: 18px 25px;
  width: 282px;
  background: #ffffff;
  border: 1px solid rgba(18, 90, 93, 0.3);
  border-radius: 5px;

  font-family: Gilroy;
  font-size: 16px;
  font-weight: 500;
  line-height: 18.83px;
  color: #222;

  transition: border 0.3s ease-in-out;

  @media (max-width: 660px) {
    font-size: 14px;
  }

  &::placeholder {
    font-family: Gilroy;
    font-size: 16px;
    font-weight: 500;
    line-height: 18.83px;
    color: #7ca5a1;

    @media (max-width: 660px) {
