    border: 1px solid red;
    color: #ff7878;
  }

  &:hover {
    border: 1px solid #125a5d;
  }

  &.input_textarea {
    resize: vertical;
    width: 100%;
    height: 150px;
    outline: none;
  }
}

.radio {
  position: relative;
  cursor: pointer;
  // padding: 12px 15px;
  display: grid;
  grid-auto-flow: column;
  align-items: center;
  gap: 10px;
  justify-content: start;
  width: max-content;

  .radio__input {
    display: none;
  }

  .radio__mark {
    position: relative;
    border-radius: 50%;
    width: 16px;
    height: 16px;
    border: 2px solid #11a0a3;
    background-color: #fff;
    z-index: 1;
  }

  .radio__mark::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 5px;
    height: 5px;
    background-color: #11a0a3;
    border-radius: 50%;
    opacity: 0;
    z-index: 1;
  }

  .radio__input:checked + .radio__mark::before {
    opacity: 1;
  }

  .radio__title {
    font-family: OpenSans;
    font-size: 16px;
    font-weight: 600;
    line-height: 20.79px;
    text-align: left;
    color: #117074;
  }

  &.radio_disabled {
    pointer-events: none;
    opacity: 0.4;
  }
}

.checkbox {
  position: relative;
  cursor: pointer;
  // padding: 12px 15px;
  display: grid;
  grid-auto-flow: column;
  align-items: center;
  gap: 10px;
  justify-content: start;
  width: max-content;

  .checkbox__input {
    display: none;
  }

  .checkbox__mark {
    position: relative;
    border-radius: 3px;
    width: 17px;
    height: 17px;
    border: 2px solid #11a0a3;
    background-color: #fff;
    z-index: 1;
  }

  .checkbox__mark::before {
    content: "";
    background-image: url(../img/mark.svg);
    background-repeat: no-repeat;
    background-size: auto;
    background-position: center;
    position: absolute;
    inset: 0;
    opacity: 0;
    z-index: 1;
  }

  .checkbox__input:checked + .checkbox__mark::before {
    opacity: 1;
  }

  .checkbox__title {
    font-family: OpenSans;
    font-size: 16px;
    font-weight: 600;
    line-height: 20.79px;
    text-align: left;
    color: #117074;
  }

  &.checkbox_disabled {
    // pointer-events: none;
    opacity: 0.4;
  }
}

.btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 16.5px 40px;
  gap: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-family: Gilroy;
  font-size: 18px;
  font-weight: 500;
  line-height: 21.83px;
  text-align: left;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out, border-color 0.3s ease-in-out;
  border: 1px solid #11a0a3;
  color: #fff;
  background: #11a0a3;
  width: max-content;

  @media (max-width: 1060px) {
    padding: 13.5px 40px;
    font-size: 17px;
  }

  @media (max-width: 760px) {
    font-size: 16px;
  }

  &.btn_gray {
    border-color: #11a0a3;
    background: #11a0a3;
    color: #fff;

    &:hover {
      border-color: #117074;
      background: #117074;
      color: #fff;
    }
  }

  &.btn_trans-gray {
    border-color: #11a0a3;
    background: transparent;
    color: #117074;

    &:hover {
      border-color: #11a0a3;
      background: #11a0a3;
      color: #fff;
    }
  }

  &.btn_trans-white {
    border-color: #fff;
    background: transparent;
    color: #fff;

    &:hover {
      border-color: #fff;
      background: #fff;
      color: #117074;
    }
  }

  &.btn_white {
    border-color: #f0fdfb;
    background: #f0fdfb;
    color: rgba(#117074, 0.5);

    &:hover {
      border-color: #737d7d;
      background: #737d7d;
      color: #fff;
      opacity: 0.7;
    }
  }

  &.btn_orange {
    border-color: #fab502;
    background: #fab502;
    color: #815e04;

    &:hover {
      border-color: #a57700;
      background: #a57700;
      color: #fff;
    }
  }

  &.btn_trans-orange {
    border-color: #fab502;
    background: transparent;
    color: #222222;
    // border-image: linear-gradient(93.1deg, #fbd676 0.28%, #fab502 68.21%) 1;

    &:hover {
      border-color: #fab502;
      background: #fab502;
      color: #815e04;
    }
  }
}

.developer {
  align-self: center;
  display: grid;
