  font-family: Inter;
  font-size: 12px;
  font-weight: 400;
  line-height: 15.6px;
  letter-spacing: -0.011em;
  text-align: left;
  color: rgba(#cdfaf6, 0.5);
  transition: color 0.3s ease-in-out;

  .developer__text {
    max-width: 140px;
  }

  &:hover {
    color: #cdfaf6;
  }
  & svg path {
    fill: rgba(#cdfaf6, 0.5);
    transition: fill 0.3s ease-in-out;
  }

  &:hover svg path {
    fill: #cdfaf6;
  }
}

