      &.nav__item_apartments {
        display: none;
      }
    }

    .nav__link {
      .nav__text {
        font-family: Gilroy;
        font-size: 18px;
        font-weight: 400;
        line-height: 20.88px;
        text-align: left;
        color: #222;
        text-shadow: transparent 0.2px 0.2px 0.2px, transparent -0.2px -0.2px 0.2px, transparent -0.2px 0.2px 0.2px,
          transparent 0.2px -0.2px 0.2px;

        transition: color 0.3s ease-in-out, font-weight 0.3s ease-in-out, text-shadow 0.3s ease-in-out;

        &:hover {
          color: #125a5d;
        }

        &:active {
          text-shadow: #125a5d 0.2px 0.2px 0.2px, #125a5d -0.2px -0.2px 0.2px, #125a5d -0.2px 0.2px 0.2px,
            #125a5d 0.2px -0.2px 0.2px;
          color: #125a5d;
        }
      }
    }
  }

  &.nav_active {
    display: flex;
    opacity: 1;
    visibility: visible;
    transform: translateX(0%);
  }
}
