.runner-wraper {
  display: flex;
  flex-flow: column nowrap;
  // align-items: center;
  // justify-content: center;
  gap: 15px;

  .runner {
    @keyframes scroll {
      from {
        transform: translateX(0);
      }
      to {
        transform: translateX(calc(-100% - 50px));
      }
    }

    .runner__container {
      max-width: 1920px;
      padding: 25px 0;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      gap: 50px;
      // border-bottom: 1px solid $color-2DA1B7;
      // border-top: 1px solid $color-2DA1B7;

      overflow: hidden;

      .runner__wrap {
        // flex-shrink: 0;
        // gap: 50px;
        // display: flex;
        // animation: scroll 10s linear infinite;
      }

      .runner__list {
        animation: scroll 60s linear infinite;
        flex-shrink: 0;
        display: flex;
        gap: 50px;
        flex-flow: row nowrap;
        align-items: center;
      }

      .runner__el {
        // flex-shrink: 0;
        // gap: 50px;
        // display: flex;
        // animation: scroll 10s linear infinite;
        font-size: 32px;
        font-weight: 300;
        line-height: 41.76px;
        letter-spacing: 0.04em;
        text-align: left;
        // color: $color-2DA1B7;
        text-transform: uppercase;
      }

      .runner__dot {
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: #2da1b7;
      }
    }

    &.runner_reverse {
      @keyframes scroll-reverse {
        from {
          transform: translateX(calc(-100% + 150px));
        }
        to {
          transform: translateX(calc(0% + 200px));
        }
      }

      .runner__list {
        animation: scroll-reverse 60s linear infinite;
      }
    }
  }
}

