      padding: 10px 10px 10px;
    }

    .header__container {
      display: grid;
      justify-content: start;
      grid-template: auto / auto 1fr auto;
      gap: 0 20px;
      grid-template-areas:
        "a e b"
        "c e b";

      @media (max-width: 1060px) {
        grid-template: auto / 1fr auto;

