// --------------------------------------- start social-btn ---------------------------------------

.btn-go-up {
  position: fixed;
  bottom: 50px;
  left: 50px;
  width: 58px;
  height: 58px;
  border-radius: 50%;
  background-color: #ecfcfa;
  display: grid;
  place-items: center;
  transition: background-color 0.3s ease-in-out;
  z-index: 1000;

  @media (max-width: 960px) {
    left: 20px;
  }

  @media (max-width: 660px) {
    left: 10px;
    width: 48px;
    height: 48px;
  }

  & svg {
    @media (max-width: 660px) {
      width: 26.5px;
      height: 26.5px;
    }
  }

  & svg path {
    stroke: #117074;
    transition: stroke 0.3s ease-in-out;
  }

  &:hover {
    background-color: #117074;
    & svg path {
      stroke: #fff;
    }
  }

  &.btn-go-up_hide {
    display: none;
  }
}

.social-btn-block {
  position: fixed;
  bottom: 50px;
  right: 50px;
  width: 58px;
  height: 58px;
  transition: all 0.5s ease-in-out;
  z-index: 1000;

  @media (max-width: 960px) {
    right: 20px;
  }

  @media (max-width: 660px) {
    right: 10px;
    width: 48px;
    height: 48px;
  }

  .social-btn__wrap {
    position: absolute;
    bottom: calc(100% + 5px);
    left: 0;
    right: 0;
    display: grid;
    gap: 5px;
    overflow: hidden;
    height: 0%;
    opacity: 0;
    transition: height 0.5s ease-in-out, opacity 0.5s ease-in-out;

    &.social-btn__wrap_active {
      opacity: 1;
      height: calc(58px + 5px + 58px + 5px + 58px);

      @media (max-width: 660px) {
        height: calc(48px + 5px + 48px + 5px + 48px);
      }
    }

    .social-btn__link {
      width: 58px;
      height: 58px;

      @media (max-width: 660px) {
        width: 48px;
        height: 48px;
      }
    }
    .social-btn__link_watsapp {
    }
    .social-btn__link_vk {
    }
    .social-btn__link_telegram {
    }
  }

  .social-btn {
    width: 100%;
    height: 100%;
    transition: all 0.5s ease-in-out;

    .social-btn__icon {
      &.social-btn__icon_hide {
        @media (max-width: 660px) {
          width: 20px;
          height: 20px;
        }

        display: none;
      }

      &.social-btn__icon_show {
        display: block;
      }
    }

    &.social-btn_active {
      transition: all 0.5s ease-in-out;

      .social-btn__icon {
        transition: all 0.5s ease-in-out;

        &.social-btn__icon_hide {
          display: block;
        }

        &.social-btn__icon_show {
          display: none;
        }
      }
    }
  }
}

.circle-btn {
  width: 58px;
  height: 58px;
  border-radius: 50%;
  background-color: #ecfcfa;
  display: grid;
  place-items: center;
  transition: background-color 0.3s ease-in-out;

  & svg {
    @media (max-width: 660px) {
      width: 26.5px;
      height: 26.5px;
    }
  }

  & .svg-fill path {
    fill: #117074;
    transition: fill 0.3s ease-in-out;
  }

  & .svg-stroke path {
    stroke: #117074;
    transition: stroke 0.3s ease-in-out;
  }

  &:hover {
    background-color: #117074;

    & .svg-fill path {
      fill: #fff;
    }

    & .svg-stroke path {
      stroke: #fff;
    }
  }
}
