        @media (max-width: 1060px) {
          height: 470px;
        }

        @media (max-width: 660px) {
          height: 315px;
        }

        .markerElement {
          width: max-content;
        }

        .marker-wrap {
          width: max-content;
          position: relative;
        }

        .marker {
          position: relative;
          // transform: translate(-50%, -100%);
          transform: translate(-37%, -90%);

