      position: absolute;
      top: 60px;
      width: 737px;
      height: 431px;
      left: 50%;
      margin-left: 180px;

      @media (max-width: 1920px) {
        left: 59.5%;
        width: 38.5vw;
        height: auto;
        margin-left: initial;
      }

      @media (max-width: 1360px) {
        width: 49.5vw;
        top: 34px;
        left: 56.5%;
      }

      @media (max-width: 660px) {
        display: none;
      }

      & img {
      }
    }
    .consultations_top-block {
      height: 100px;

      @media (max-width: 1360px) {
        height: 80px;
      }

      @media (max-width: 660px) {
        height: 30px;
      }
    }
    .consultations_bot-block {
      padding: 100px 20px;
      background: linear-gradient(99.41deg, #f0fdfb 6.27%, #cdfaf6 92.06%);

      @media (max-width: 1360px) {
        padding: 80px 20px;
      }

      @media (max-width: 660px) {
        padding: 60px 10px;
      }

      .container {
        .consultations__container {
        }

        .consultations__title {
          margin-bottom: 20px;
        }

        .consultations__text {
          margin-bottom: 50px;
          max-width: 600px;
          font-family: OpenSans;
          font-size: 18px;
          font-weight: 400;
          line-height: 24.51px;
          text-align: left;
          color: #585858;

          @media (max-width: 1360px) {
            max-width: 400px;
            font-size: 17px;
            line-height: 23.15px;
          }
        }

        .consultations__form {
          display: grid;
          grid-auto-flow: column;
          gap: 25px;
          margin-bottom: 7px;
          justify-content: start;

          @media (max-width: 1360px) {
            margin-bottom: 14px;
          }

          @media (max-width: 660px) {
            grid-auto-flow: row;
            // max-width: 282px;
            justify-content: initial;
            gap: 14px;
            margin-bottom: 15px;
          }

          .input {
            @media (max-width: 1360px) {
              padding: 14px 40px;
            }

            @media (max-width: 660px) {
              padding: 12px 15px;
              max-width: 283px;
              width: 100%;
            }
          }
          .consultations__btn {
            padding: 18px 40px;

            @media (max-width: 1360px) {
              padding: 14px 40px;
            }

            @media (max-width: 660px) {
              padding: 12px 15px;
              max-width: 283px;
              width: 100%;
            }
          }
        }
      }
    }
  }
