.news-page {
  .news-page__section {
    padding: 49px 20px 100px;

    @media (max-width: 1060px) {
      padding: 40px 20px 80px;
    }

    @media (max-width: 660px) {
      padding: 30px 10px 60px;
    }
    .container {
      display: grid;
      gap: 25px;

      .news-page__list {
        display: grid;
        gap: 25px;
        grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));

        @media (max-width: 1060px) {
          grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
          gap: 15px;
        }
        .news-page__item {
          /* Тень карточек преимуществ */
          box-shadow: 4px 4px 8px rgba(30, 109, 109, 0.06), 1px 1px 3px rgba(8, 72, 72, 0.07);
          border-radius: 10px;
          overflow: hidden;

          .news-page__item-img {
            width: 100%;
            height: 300px;
            overflow: hidden;
          }
          .news-page__item-text-wrapper {
            background: #f0fdfb;
            padding: 25px;
            display: grid;
            grid-template-rows: auto 1fr auto;
            gap: 10px;
            height: calc(100% - 300px);

            @media (max-width: 1060px) {
              padding: 20px;
            }

            @media (max-width: 660px) {
              padding: 15px;
            }

            .news-page__item-title {
              font-family: Gilroy;
              font-size: 24px;
              font-weight: 600;
              line-height: 29.4px;
              text-align: left;
              color: #052b2e;

              @media (max-width: 660px) {
                font-size: 20px;
                line-height: 26.4px;
              }
            }
            .news-page__item-text {
              font-family: OpenSans;
              font-size: 16px;
              font-weight: 400;
              line-height: 21.79px;
              text-align: left;
              color: #585858;

              @media (max-width: 1060px) {
                font-size: 15px;
                line-height: 20.6px;
              }
            }

            .news-page__item-btn {
              border-radius: 10px;
              margin-top: 50px;
            }
          }
        }
      }
    }
  }
}

// --------------------------------------- NEWS end ---------------------------------------
